body {
  background-color: #f5f5f5;
}

.photo-card {
  width: 300px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  display: inline-block;
}

.photo-card img {
  width: 100%;
  margin-bottom: 10px;
}

.info {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.info-item {
  gap: 5px;
  margin: 5px 0;
  font-size: small;
  display: grid;
}

.search-form {
  text-align: center;
  margin-bottom: 20px;
}

.search-form input[type="text"] {
  width: 300px;
  border-radius: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 10px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

#end-message {
  text-align: center;
  color: #4caf50;
  margin-top: 20px;
  font-size: 20px;
}

